<template>
  <div id="app">
    
     <div id="front">
            <nav class="navbar navbar-default" role="navigation">
                <!-- Brand and toggle get grouped for better mobile display -->
                <div class="navbar-header">
                    <button type="button" class="navbar-toggle" data-toggle="collapse" data-target=".navbar-ex1-collapse">
                        <span class="sr-only">Toggle navigation</span>
                        <span class="icon-bar"></span>
                        <span class="icon-bar"></span>
                        <span class="icon-bar"></span>
                    </button>
                    <router-link class="navbar-brand" to="/"></router-link>
                </div>
            
                <!-- Collect the nav links, forms, and other content for toggling -->
                <div class="collapse navbar-collapse navbar-ex1-collapse">
                    <ul class="nav navbar-nav">
                        <li><router-link to="/blog_list_page"></router-link></li>
                    </ul>
                    <form class="navbar-form navbar-left" role="search">
                        <div class="form-group">
                            <input type="text" class="form-control" placeholder="">
                        </div>
                        <button type="submit" class="btn btn-default"></button>
                    </form>
                    <ul class="nav navbar-nav navbar-right">
                        <li><router-link to="/write_blog"></router-link></li>
                        <li v-show="is_logout"><router-link to="/login"></router-link></li>
                        <li v-show="is_logout"><router-link to="/regist"></router-link></li>

                        <li class="dropdown" v-show="is_login">
                            <a href="#" class="dropdown-toggle" data-toggle="dropdown">{{username}}<b class="caret"></b></a>
                            <ul class="dropdown-menu">
                                <li><router-link to="/user_detail"></router-link></li>
                                <li><a href="#" v-on:click.prevent="logout"></a></li>                 
                            </ul>
                        </li>
                    </ul>
                </div><!-- /.navbar-collapse -->
            </nav>
      </div>
      <div id="container" class="container">
        <router-view></router-view>
      </div>
  </div>
</template>

<script>
import './assets/js/jquery-3.2.1'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min.js'

export default {
  name: 'App',
  data: function () {
      return {
          cookie: document.cookie
      }
  },
  methods: {
      logout: function () {
          document.cookie =  "username=false";
          this.cookie = "username=false";
          document.location = "http://localhost:3000";
      }
  },
  computed: {
      is_login: function () {
          if (this.cookie != "username=false") {
              return true;
          }
          else {
              return false;
          }
      },
      is_logout: function () {
          if (this.cookie == "username=false") {
              return true;
          }
          else {
              return false;
          }
      },
      username: function () {
          if (this.cookie == "username=false") {
              return "";
          }
          else {
              return this.cookie.replace(/username=/, "");
          }
      }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



